<template>
    <v-layout align-start>
        <v-flex>
  
            <v-toolbar text color="white">
                <v-toolbar-title>Usuarios</v-toolbar-title>
                <v-divider
                class="mx-2"
                inset
                vertical
                ></v-divider>
                <v-spacer></v-spacer>
                <v-text-field class="text-xs-center" v-model="search"  append-icon="mdi-magnify" 
                label="Bsqueda" single-line hide-details></v-text-field>
                <v-spacer></v-spacer>
                <v-dialog v-model="dialog" max-width="500px">
                <template v-slot:activator="{ on }">
                    <v-btn color="primary" dark class="mb-2" v-on="on">Nuevo</v-btn>
                </template>
                <v-card>
                    <v-card-title>
                    <span class="headline">{{ formTitle }}</span>
                    </v-card-title>
        
                    <v-card-text>
                    <v-container grid-list-md>
                        <v-layout wrap>
                                <v-flex xs12 sm6 md6>
                                    <v-text-field v-model="nombre" label="Nombre">                                        
                                    </v-text-field>
                                </v-flex>
                                <v-flex xs12 sm6 md6>
                                    <v-select v-model="rol"
                                    :items="roles" label="Rol">
                                    </v-select>
                                </v-flex>
                                <v-flex xs12 sm6 md6>
                                    <v-select v-model="tipo_documento"
                                    :items="documentos" label="Tipo Documento">
                                    </v-select>
                                </v-flex>
                                <v-flex xs12 sm6 md6>
                                    <v-text-field v-model="num_documento" label="Nmero Documento">
                                    </v-text-field>
                                </v-flex>
                                <v-flex xs12 sm6 md6>
                                    <v-text-field v-model="direccion" label="Direccin">
                                    </v-text-field>
                                </v-flex>
                                <v-flex xs12 sm6 md6>
                                    <v-text-field v-model="telefono" label="Telfono">
                                    </v-text-field>
                                </v-flex>
                                <v-flex xs12 sm6 md6>
                                    <v-text-field v-model="email" label="Email">
                                    </v-text-field>
                                </v-flex>
                                <v-flex xs12 sm6 md6>
                                    <v-text-field type="password" v-model="password" label="Password">
                                    </v-text-field>
                                </v-flex>
                                <v-flex xs12 sm12 md12 v-show="valida">
                                    <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v">

                                    </div>
                                </v-flex>
                            </v-layout>
                    </v-container>
                    </v-card-text>
        
                    <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" text @click="close">Cancelar</v-btn>
                    <v-btn color="blue darken-1" text @click="guardar()">Guardar</v-btn>
                    </v-card-actions>
                </v-card>
                </v-dialog>
                <v-dialog v-model="adModal" max-width="290">
                  <v-card>
                    <v-card-title class="headline" v-if="adAccion==1">
                      Activar Item
                    </v-card-title>
                    <v-card-title class="headline" v-if="adAccion==2">
                      Desactivar Item
                    </v-card-title>
                    <v-card-text>
                      Ests a punto de <span v-if="adAccion==1">activar</span>
                      <span v-if="adAccion==2"> desactivar</span> el item {{ adNombre }}
                    </v-card-text>
                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn @click="cerrarModalActivarDesactivar()" color="green darken-1" text="text">
                        Cancelar
                      </v-btn>
                      <v-btn v-if="adAccion==1" @click="activar()" color="orange darken-4" text="text">
                        Activar
                      </v-btn>
                      <v-btn v-if="adAccion==2" @click="desactivar()" color="orange darken-4" text="text">
                        Desactivar
                      </v-btn>
  
                    </v-card-actions>
                  </v-card>
                </v-dialog>
            </v-toolbar>
  
            
        <v-data-table
            :headers="headers"
            :items="usuarios"
            :search="search"
            sort-by="calories"
            class="elevation-1"
        >
        <template v-slot:item.opciones="{ item }" >
            <td class="justify-center layout px-0">
                <v-icon
                    small
                    class="mr-2"
                    @click="editItem(item)"
                    >
                    mdi-pencil
                    </v-icon>
                    <template v-if="item.estado">
                      <v-icon
                      small
                      @click="activarDesactivarMostrar(2,item)"
                      >
                      mdi-block-helper
                    </v-icon>
                  </template>
                  <template v-else>
                      <v-icon
                      small
                      @click="activarDesactivarMostrar(1,item)"
                      >
                      mdi-check
                    </v-icon>
                  </template>
            </td>
          </template>
          <template v-slot:items="props">
            <td>{{ props.item.nombre }}</td>
            <td>{{ props.item.rol }}</td>
            <td>{{ props.item.tipo_documento }}</td>
            <td>{{ props.item.num_documento }}</td>
            <td>{{ props.item.direccion }}</td>
            <td>{{ props.item.telefono }}</td>
            <td>{{ props.item.email }}</td>
           
                      
        </template>
  
        <template v-slot:item.estado="{ item }">
  
          <td>
               <div v-if="item.estado">
                  <span class="blue--text">Activo</span>
               </div>
                <div v-else>
                    <span class="red--text">Inactivo</span>
                </div>
          </td>
        </template>
        <template v-slot:no-data>
            <v-btn
            color="primary"
            @click="listar()"
            >
            Resetear
            </v-btn>
        </template>
           
       
       
        
        </v-data-table>
  
  
        </v-flex>
   </v-layout>
  </template>
  <script>
  import axios from 'axios'
  export default {
    data(){
        return{
            dialog: false,
            search:'',
            usuarios:[],
            dialogDelete: false,
            headers: [
           
            { text: 'Opciones', value: 'opciones',sortable: false },
            { text: 'Nombre', value: 'nombre' , sortable: true},
            { text: 'Rol', value: 'rol', sortable: true},
            { text: 'Tipo de documento', value: 'tipo_documento' },
            { text: 'N Documento', value: 'num_documento', sortable: false },
            { text: 'Direccin', value: 'direccion', sortable: false },
            { text: 'Telfono', value: 'telefono', sortable: false },
            { text: 'Email', value: 'email', sortable: false },
            { text: 'N Documento', value: 'num_documento', sortable: false },
            { text: 'Estado', value: 'estado', sortable: false },
           
  
            ],
           
            editedIndex: -1,
            _id:'',
            nombre:'',
            rol:'',
            roles:['administrador','almacenero','vendedor'],
            tipo_documento:'',
            documentos:['DNI','RUC','PASAPORTE', 'CEDULA'],
            num_documento:'',
            direccion:'',
            telefono:'',
            email:'',
            password:'',
            valida: 0,
            validaMensaje:[],
            adModal:0,
            adAccion:0,
            adNombre:'',
            adId: ''
            
        }
    },
    computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'Nuevo registro' : 'Editar registro'
    },
  },
  
  watch: {
    dialog (val) {
      val || this.close()
    },
    dialogDelete (val) {
      val || this.closeDelete()
    },
  },
  
  created () {
    this.listar()
  },
  
  methods: {
  
    listar(){
        let me = this;
        let header={"Token": this.$store.state.token};
        let configuracion={headers : header}
  
        axios.get('usuario/list', configuracion).then(function(response){
            //console.log(response, 'xd')
            me.usuarios=response.data;
        }).catch(function(error){
            console.log(error)
        });
    },
    limpiar(){
                this._id='';
                this.nombre='';
                this.num_documento='';
                this.direccion='';
                this.telefono='';
                this.email='';
                this.password='';
                this.valida=0;
                this.validaMensaje=[];
                this.editedIndex=-1;
            },
  
    validar(){
                this.valida=0;
                this.validaMensaje=[];
                if(!this.rol){
                    this.validaMensaje.push('Seleccione un rol.');
                }
                if(this.nombre.length<1 || this.nombre.length>50){
                    this.validaMensaje.push('El nombre del usuario debe tener entre 1-50 caracteres.');
                }
                if(this.num_documento.length>20){
                    this.validaMensaje.push('El documento no debe tener ms de 20 caracteres.');
                }
                if(this.direccion.length>70){
                    this.validaMensaje.push('La direccin no debe tener ms de 70 caracteres.');
                }
                if(this.telefono.length>20){
                    this.validaMensaje.push('El telfono no debe tener ms de 20 caracteres.');
                }
                if(this.email.length<1 || this.nombre.length>50){
                    this.validaMensaje.push('El email del usuario debe tener entre 1-50 caracteres.');
                }
                if(this.password.length<1 || this.nombre.length>64){
                    this.validaMensaje.push('El password del usuario debe tener entre 1-64 caracteres.');
                }
                if (this.validaMensaje.length){
                    this.valida=1;
                }
                return this.valida;
            },
  
            guardar(){
                let me=this;
                let header={"Token" : this.$store.state.token};
                let configuracion= {headers : header};
                if (this.validar()){
                    return;
                }
                if (this.editedIndex >-1){
                    //Cdigo para editar
                    axios.put('usuario/update',{
                        '_id':this._id,
                        'rol':this.rol,
                        'nombre':this.nombre,
                        'tipo_documento':this.tipo_documento,
                        'num_documento':this.num_documento,
                        'direccion':this.direccion,
                        'telefono': this.telefono,
                        'email':this.email,
                        'password':this.password
                    },configuracion)
                    .then(function(response){
                        me.limpiar();
                        me.close();
                        me.listar();
                    })
                    .catch(function(error){
                        console.log(error);
                    });
                }else{
                    //Cdigo para guardar
                    axios.post('usuario/add',
                    {
                        'rol':this.rol,
                        'nombre':this.nombre,
                        'tipo_documento':this.tipo_documento,
                        'num_documento':this.num_documento,
                        'direccion':this.direccion,
                        'telefono': this.telefono,
                        'email':this.email,
                        'password':this.password
                    },configuracion)
                    .then(function(response){
                        me.limpiar();
                        me.close();
                        me.listar();
                    })
                    .catch(function(error){
                        console.log(error);
                    });
                }
            },
   
    editItem (item) {
  
        this._id=item._id;
        this.rol=item.rol;
        this.nombre=item.nombre;
        this.tipo_documento=item.tipo_documento;
        this.num_documento=item.num_documento;
                this.direccion=item.direccion;
                this.telefono=item.telefono;
                this.email=item.email;
                this.password=item.password;
                this.dialog = true;
                this.editedIndex=1;
    },
  
   
  
    activarDesactivarMostrar(accion, item){
        this.adModal=1
        this.adNombre= item.nombre
        this.adId=item._id
      if (accion==1) {
       this.adAccion=1
      } else if(accion==2){
        this.adAccion=2
      }else{
        this.adModal=0
      }
    },
    cerrarModalActivarDesactivar(){
      this.adModal=0
    },
  
    activar(){
      let me = this;
      let header={"Token": this.$store.state.token};
      let configuracion={headers : header}
      axios.put('usuario/activate', {'_id': this.adId}, configuracion)
        .then(function(response){
          me.adModal=0
          me.adAccion=0
          me.adNombre=''
          me.adId=''
          me.listar()
  
        }).catch(function(error){
          console.log(error)
        });
  
  
    },
    desactivar(){
      let me = this;
      let header={"Token": this.$store.state.token};
      let configuracion={headers : header}
      axios.put('usuario/desactivate', {'_id': this.adId}, configuracion)
        .then(function(response){
          me.adModal=0
          me.adAccion=0
          me.adNombre=''
          me.adId=''
          me.listar()
  
        }).catch(function(error){
          console.log(error)
        });
  
  
    },
  
   
  
    close () {
      this.dialog = false
      this.limpiar()
      
     
    },
  
   
  },
  
  
  }
  
  </script>
  