<template>
    <v-layout align-start>
        <v-flex>
  
            <v-toolbar text color="white">
                <v-toolbar-title>Ventas</v-toolbar-title>
                <v-divider
                class="mx-2"
                inset
                vertical
                ></v-divider>
                <v-spacer></v-spacer>
                <v-text-field class="text-xs-center" v-model="search"  append-icon="mdi-magnify" 
                label="Bsqueda" single-line hide-details v-if="verNuevo==0"></v-text-field>
                <v-spacer></v-spacer>
                <v-btn  @click="mostrarNuevo()" color="primary" dark class="mb-2" v-if="verNuevo==0">Nuevo</v-btn>
                <v-dialog v-model="dialog" max-width="1000px">
                
                <v-card>
                    <v-card-title>
                    <span class="headline">Seleccione un artculo</span>
                    </v-card-title>
        
                    <v-card-text>
                    <v-container grid-list-md>
                        <v-layout wrap>
                          <v-flex xs12 sm12 md12 lg12 xl12>
                            <v-text-field v-model="texto" @keyup.enter="listarArticulo()" class="text-xs-center" append-icon="mdi-magnify"  label="Bsqueda">

                            </v-text-field>
                            <template>
                              <v-data-table
                                  :headers="cabeceraArticulos"
                                  :items="articulos"
                                 
                                  sort-by="calories"
                                  class="elevation-1"
                              >
                              <template v-slot:item.seleccionar="{ item }" >
                                  <td class="justify-center layout px-0">
                                      <v-icon
                                          small
                                          class="mr-2"
                                          @click="agregarDetalle(item)"
                                          >
                                          mdi-format-annotation-plus
                                         </v-icon>
        
                                  </td>
                                </template>
                                <template v-slot:items="props">
                                  <td>{{ props.item.codigo }}</td>
                                  <td>{{ props.item.nombre }}</td>
                                  <td>{{ props.item.categoria.nombre }}</td>
                                  <td>{{ props.item.stock }}</td>
                                  <td>{{ props.item.precio_venta }}</td>
                                  <td>{{ props.item.descripcion }}</td>
                                
                                            
                              </template>

                              <template v-slot:item.estado="{ item }">

                                <td>
                                    <div v-if="item.estado">
                                        <span class="blue--text">Activo</span>
                                    </div>
                                      <div v-else>
                                          <span class="red--text">Inactivo</span>
                                      </div>
                                </td>
                              </template>
                              
                                
                            
                            
                              
                            </v-data-table>
                            </template>
                          </v-flex>
    
                        </v-layout>
                    </v-container>
                    </v-card-text>
        
                    <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" text @click="close">Cancelar</v-btn>
                    </v-card-actions>
                </v-card>
                </v-dialog>
                <v-dialog v-model="adModal" max-width="290">
                  <v-card>
                    <v-card-title class="headline" v-if="adAccion==1">
                      Activar Item
                    </v-card-title>
                    <v-card-title class="headline" v-if="adAccion==2">
                      Desactivar Item
                    </v-card-title>
                    <v-card-text>
                      Ests a punto de <span v-if="adAccion==1">activar</span>
                      <span v-if="adAccion==2"> desactivar</span> el item {{ adNombre }}
                    </v-card-text>
                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn @click="cerrarModalActivarDesactivar()" color="green darken-1" text="text">
                        Cancelar
                      </v-btn>
                      <v-btn v-if="adAccion==1" @click="activar()" color="orange darken-4" text="text">
                        Activar
                      </v-btn>
                      <v-btn v-if="adAccion==2" @click="desactivar()" color="orange darken-4" text="text">
                        Desactivar
                      </v-btn>
  
                    </v-card-actions>
                  </v-card>
                </v-dialog>
                <v-dialog v-model="comprobanteModal" max-width="1000px">
                  <v-card>
                    <v-card-title class="headline">
                      <v-btn @click="crearPdf()">
                        <v-icon>
                          mdi-printer
                        </v-icon>
                      </v-btn>
                      Reporte de venta
                    </v-card-title>
                    <v-card-text>

                      <div id="factura">

                        <header>
                          <div id="logo">
                              <img src="data:image/png;base64," id="imagen">
                          </div>
                          <div id="datos">
                              <p id="encabezado">
                                  <b>IncanatoIT</b><br>Jos Glvez 1368, Chongoyape - Chiclayo, Per<br>Telefono:(+51)931742904<br>Email:jcarlos.ad7@gmail.com
                              </p>
                          </div>
                          <div id="fact">
                              <p>{{tipo_comprobante}}<br>
                              {{serie_comprobante}}-{{ num_comprobante }}<br>
                             {{ fecha }}</p>
                          </div>
                      </header>
                      <br>
                      <section>
                          <div>
                              <table id="facliente">
                                  <tbody>
                                      <tr>
                                          <td id="cliente">
                                              <strong>Sr(a). {{ persona.nombre }}</strong><br>
                                              <strong>Documento:</strong> {{ persona.num_documento}}<br>
                                              <strong>Direccin:</strong>{{persona.direccion}}<br>
                                              <strong>Telfono:</strong> {{ persona.telefono }}<br>
                                              <strong>Email:</strong> j{{persona.email}}
                                          </td>
                                      </tr>
                                  </tbody>
                              </table>
                          </div>
                      </section>
                      <br>
                      <section>
                          <div>
                              <table id="facarticulo">
                                  <thead>
                                      <tr id="fa">
                                          <th>CANT</th>
                                          <th>DESCRIPCION</th>
                                          <th>PRECIO UNIT</th>
                                          <th>DESC.</th>
                                          <th>PRECIO TOTAL</th>
                                      </tr>
                                  </thead>
                                  <tbody>
                                      <tr v-for="det in detalles" :key="det._id">
                                          <td style="text-align:center;">{{ det.cantidad }}</td>
                                          <td>{{ det.articulo }}</td>
                                          <td style="text-align:right;"> {{ det.precio }}</td>
                                          <td style="text-align:right;">{{ det.descuento }}</td>
                                          <td style="text-align:right;">{{ (det.cantidad * det.precio) - det.descuento}}</td>
                                      </tr>
                                  </tbody>
                                  <tfoot>
                                      <tr>
                                          <th></th>
                                          <th></th>
                                          <th></th>
                                          <th style="text-align:right;">SUBTOTAL</th>
                                          <th style="text-align:right;">$ {{ totalParcial=(total-totalImpuesto).toFixed(2) }}</th>
                                      </tr>
                                      <tr>
                                          <th></th>
                                          <th></th>
                                          <th></th>
                                          <th style="text-align:right;">IVA({{ impuesto }})</th>
                                          <th style="text-align:right;"> {{ totalImpuesto=((total*impuesto)/(1+impuesto)).toFixed(2) }}</th>
                                      </tr>
                                      <tr>
                                          <th></th>
                                          <th></th>
                                          <th></th>
                                          <th style="text-align:right;">TOTAL</th>
                                          <th style="text-align:right;">${{ total= calcularTotal }}</th>
                                      </tr>
                                  </tfoot>
                              </table>
                          </div>
                      </section>
                      <br>
                      <br>
                      <footer>
                          <div id="gracias">
                              <p><b>Gracias por su compra!</b></p>
                          </div>
                      </footer>

                      </div>


                    </v-card-text>
                    <v-card-actions>
                      <v-btn @click="ocultarComprobante()" color="blue darken-1"> Cancelar</v-btn>
                    </v-card-actions>

                  </v-card>

                </v-dialog>
            </v-toolbar>
  
            
        <v-data-table
            :headers="headers"
            :items="ventas"
            :search="search"
            sort-by="calories"
            class="elevation-1"
            v-if="verNuevo==0"
        >
        <template v-slot:item.opciones="{ item }" >
            <td class="justify-center layout px-0">
                    <v-icon small class="mr-2" @click="verIngreso(item)">
                       mdi-tab
                    </v-icon>
                    <v-icon small class="mr-2" @click="mostrarComprobante(item)">
                       mdi-printer
                    </v-icon>
                    <template v-if="item.estado">
                      <v-icon
                      small
                      @click="activarDesactivarMostrar(2,item)"
                      >
                      mdi-block-helper
                    </v-icon>
                  </template>
                  <template v-else>
                      <v-icon
                      small
                      @click="activarDesactivarMostrar(1,item)"
                      >
                      mdi-check
                    </v-icon>
                  </template>
            </td>
          </template>
          <template v-slot:items="props">
            <td>{{ props.item.usuario.nombre }}</td>
            <td>{{ props.item.persona.nombre }}</td>
            <td>{{ props.item.tipo_comprobante }}</td>
            <td>{{ props.item.serie_comprobante }}</td>
            <td>{{ props.item.createdAt }}</td>
            <td>{{ props.item.impuesto }}</td>
            <td>{{ props.item.total }}</td>
           
                      
        </template>
  
        <template v-slot:item.estado="{ item }">
  
          <td>
               <div v-if="item.estado">
                  <span class="blue--text">Aceptado</span>
               </div>
                <div v-else>
                    <span class="red--text">Anulado</span>
                </div>
          </td>
        </template>
        <template v-slot:no-data>
            <v-btn
            color="primary"
            @click="listar()"
            >
            Resetear
            </v-btn>
        </template>
           
        </v-data-table>
        <v-container grid-list-sm class="pa-4 white" v-if="verNuevo" >
          <v-layout row wrap>
            <v-flex xs12 sm4 md4 lg4 xl4>
              <v-select v-model="tipo_comprobante" 
              :items="comprobantes" label="Tipo de comprobante">

              </v-select>

            </v-flex>
            <v-flex xs12 sm4 md4 lg4 xl4>
              <v-text-field v-model="serie_comprobante" label="Serie de comprobante"></v-text-field>
            </v-flex>
            <v-flex xs12 sm4 md4 lg4 xl4>
              <v-text-field v-model="num_comprobante" label="Nmero de comprobante"></v-text-field>
            </v-flex>
            <v-flex xs12 sm8 md8 lg8 xl8>
              <v-autocomplete v-model="persona" :items="personas" label="Cliente"></v-autocomplete>
            </v-flex>
            <v-flex xs12 sm4 md4 lg4 xl4>
              <v-text-field type="number" v-model="impuesto" label="Impuesto"></v-text-field>
            </v-flex>
            <v-flex xs12 sm8 md8 lg8 xl8>
              <v-text-field  v-model="codigo" label="Cdigo" @keyup.enter="buscarCodigo()"></v-text-field>
            </v-flex>
            <v-flex xs12 sm2 md2 lg2 xl2>
              <v-btn small fab dark color="teal" @click="mostrarModalArticulos()">
                <v-icon dark>mdi-format-list-bulleted-square</v-icon>
              </v-btn>
            </v-flex>
            <v-flex xs12 sm2 md2 lg2 xl2 v-show="errorArticulo">
              <div class="red--text" v-text="errorArticulo">

              </div>
            </v-flex>

            <v-flex xs12 sm12 md12 lg12 xl12>
              <template>
                <v-data-table
                :headers="cabeceraDetalles"
                :items="detalles"
                hide-actions
                
                class="elevation-1"
                >
                <template v-slot:item.borrar="{ item }">
                  <td>
                    <v-icon
                    small
                    class="mr-2"
                    @click="eliminarDetalle(detalles, item)"
                    >
                    mdi-delete-alert

                    </v-icon>
                  </td>
                </template>
                
                <template v-slot:item.cantidad="{ item }">
                  <td class="text-xs-center">{{ articulo }}</td>
                  <td class="text-xs-center"><v-text-field type="number" v-model="item.cantidad"></v-text-field></td>
                </template>
                <template v-slot:item.precio="{ item }">
                  <td class="text-xs-center"><v-text-field v-model="item.precio" type="number"></v-text-field></td>
                </template>
                <template v-slot:item.descuento="{ item }">
                  <td class="text-xs-center"><v-text-field v-model="item.descuento" type="number"></v-text-field></td>
                </template>

                <template v-slot:item.subtotal="{ item }">
                 
                  <td class="text-xs-center">$ {{ (item.cantidad * item.precio) - item.descuento }}</td>
                </template>
                <template slot="no-data">
                  <h3>No hay articulos agregados al detalle</h3>
                </template>
                
                </v-data-table>
                <v-flex class="text-xs-right">
                  <strong>Total Parcial:</strong>$ {{ totalParcial=(total-totalImpuesto).toFixed(2) }}
                </v-flex>
                <v-flex class="text-xs-right">
                  <strong>Total Impuesto:</strong>$ {{ totalImpuesto=((total*impuesto)/(1+impuesto)).toFixed(2)}}
                </v-flex>
                <v-flex class="text-xs-right">
                  <strong>Total Neto:</strong>$ {{ total=calcularTotal }}
                </v-flex>
              </template>
            </v-flex>
            <v-flex xs12 sm12 md12 v-show="valida">
              <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v">

              </div>
            </v-flex>
            <v-flex xs12 sm12 md12 lg12 xl12>
              <v-btn color="blue darken-1" flat @click.native="ocultarNuevo()">Cancelar</v-btn>
              <v-btn color="success" v-if="verDetalle==0" @click.native="guardar()">Guardar</v-btn>
             
            </v-flex>

          </v-layout>


        </v-container>
  </v-flex>
   </v-layout>
  </template>
  <script>
  import axios from 'axios'
  import jsPDF from 'jspdf';
  import html2canvas from 'html2canvas'
  export default {
    data(){
        return{
            dialog: false,
            search:'',
            ventas:[],
            dialogDelete: false,
            headers: [
           
            { text: 'Opciones', value: 'opciones',sortable: false },
            { text: 'Usuario', value: 'usuario.nombre' , sortable: true},
            { text: 'Cliente', value: 'persona.nombre', sortable: true},
            { text: 'Tipo de comprobante', value: 'tipo_comprobante' },
            { text: 'Serie comprobante', value: 'serie_comprobante', sortable: false },
            { text: 'N Comprobante', value: 'num_comprobante', sortable: false },
            { text: 'Fecha', value: 'createdAt' , sortable: true},
            { text: 'Impuesto', value: 'impuesto', sortable: false },
            { text: 'Total', value: 'total', sortable: false },
            { text: 'Estado', value: 'estado', sortable: false },
           
  
            ],
           
            verNuevo:0,
            errorArticulo: null,
            articulos:[],
            texto:'',
            cabeceraArticulos: [
         
                { text: 'Seleccionar', value: 'seleccionar',sortable: false },
                { text: 'Codigo', value: 'codigo'},
                { text: 'Nombre', value: 'nombre',sortable: true},
                { text: 'Categora', value: 'categoria.nombre',sortable: true},
                { text: 'Stock', value: 'stock'},
                { text: 'Precio Venta', value: 'precio_venta', sortable: false},
                { text: 'Descripcin', value: 'descripcion', sortable: false},
                { text: 'Estado', value: 'estado', sortable: false },
                

             ],
            _id:'',
            nombre:'',
            persona:'',
            personas:[],
            total:0,
            totalParcial:0,
            totalImpuesto:0,
            tipo_comprobante:'',
            comprobantes:['BOLETA','FACTURA','TICKET', 'GUIA'],
            serie_comprobante:'',
            num_comprobante:'',
            impuesto: 0.18,
            codigo:'',
            cabeceraDetalles:[
            { text: 'Borrar', value: 'borrar', sortable: false },
            { text: 'Articulo', value: 'articulo', sortable: false },
            { text: 'Cantidad', value: 'cantidad', sortable: false },
            { text: 'Precio', value: 'precio', sortable: false },
            { text: 'Descuento', value: 'descuento', sortable: false },
            { text: 'Sub total', value: 'subtotal', sortable: false },
            ],
            detalles:[],
            verDetalle:0,
            valida: 0,
            validaMensaje:[],
            adModal:0,
            adAccion:0,
            adNombre:'',
            adId: '',
            comprobanteModal:0,
            fecha: null
            
        }
    },
    computed: {
   
    calcularTotal: function(){
      let resultado=0.0;
      for(var i=0; i<this.detalles.length;i++){
        resultado=resultado+((this.detalles[i].cantidad*this.detalles[i].precio)-this.detalles[i].descuento)
      }
      return resultado;
    }
  },
  
  watch: {
    dialog (val) {
      val || this.close()
    },
    dialogDelete (val) {
      val || this.closeDelete()
    },
  },
  
  created () {
    this.listar()
    this.listarProveedor()
  },
  
  methods: {
    crearPdf(){
      var quotes = document.getElementById('factura')
      html2canvas(quotes).then(function(canvas){
        var imgData= canvas.toDataURL('image/png')
        var imgWidth= 210;
        var pageHeight =295;
        var imgHeight = canvas.height * imgWidth / canvas.width
        var heightLeft= imgHeight
        var doc = new jsPDF('p','mm','a4')
        var position =0
        doc.addImage(imgData,'PNG', 0, position, imgWidth, imgHeight);
        heightLeft-=pageHeight
        while(heightLeft >=0){
          position = heightLeft - imgHeight
          doc.addPage();
          doc.addImage(imgData, 'PNG', 0, position, imgWidth, imgHeight);
          heightLeft-=pageHeight;

        }

        doc.save('comprobanteVenta.pdf')



      })
    },

    mostrarComprobante(item){
      this.limpiar();
      this.tipo_comprobante=item.tipo_comprobante;
      this.serie_comprobante=item.serie_comprobante;
      this.num_comprobante=item.num_comprobante;
      this.fecha= item.createdAt,
      this.persona=item.persona;
      this.impuesto=item.impuesto;
      this.listarDetalles(item._id);
      this.comprobanteModal=1
    },
    ocultarComprobante(){
    this.comprobanteModal=0
    },
  
    listar(){
        let me = this;
       
        let header={"Token": this.$store.state.token};
        let configuracion={headers : header}
  
        axios.get('venta/list', configuracion).then(function(response){
            //console.log(response, 'xd')
            
            me.ventas=response.data;
            console.log(this.ingresos, 'xd')
        }).catch(function(error){
            console.log(error)
        });
    },

    listarArticulo(){
      let me = this;
      let header={"Token": this.$store.state.token};
      let configuracion={headers : header}

      axios.get('articulo/list?valor='+this.texto, configuracion).then(function(response){
          //console.log(response, 'xd')
          me.articulos=response.data;
      }).catch(function(error){
          console.log(error)
      })
    },

    mostrarModalArticulos(){
      this.dialog=1
    },
    
    listarDetalles(id){
           let me=this;
           let header={"Token" : this.$store.state.token};
           let configuracion= {headers : header};            
           axios.get('venta/query?_id='+id,configuracion).then(function (response){
           me.detalles=response.data.detalles;
         }).catch(function(error){
        console.log(error);
       });
     },

     verIngreso(item){
                this.limpiar();
                this.tipo_comprobante=item.tipo_comprobante;
                this.serie_comprobante=item.serie_comprobante;
                this.num_comprobante=item.num_comprobante;
                this.persona=item.persona._id;
                this.impuesto=item.impuesto;
                this.listarDetalles(item._id);
                this.verNuevo=1;
                this.verDetalle=1;
            },
    buscarCodigo(){
        let me = this;
        me.errorArticulo=null;
       
        let header={"Token": this.$store.state.token};
        let configuracion={headers : header}
  
        axios.get('articulo/queryCodigo?codigo='+this.codigo, configuracion).then(function(response){
            //console.log(response, 'xd')
            
           me.agregarDetalle(response.data)
            
        }).catch(function(error){
            me.errorArticulo='No existe el articulo'
        });
    },

    agregarDetalle(data){
      this.errorArticulo=null;
      if(this.encuentra(data._id)==true){
        this.errorArticulo='El articulo ya ha sido agregado'
      }else{
        this.detalles.push(
        {
          _id:data._id,
          articulo:data.nombre,
          cantidad:1,
          precio:data.precio_venta,
          descuento:0
        }
      )
      this.codigo='';
      }
      

    },

    encuentra(id){
      let sw=0;
      for(var i=0;i<this.detalles.length;i++){
        if(this.detalles[i]._id==id){
          sw=true
        }
      }
      return sw;
    },

    eliminarDetalle(arr, item){
      let i=arr.indexOf(item);
      if(i!= -1){
        arr.splice(i,1)
      }
    },

    listarProveedor(){
        let me = this;
        let personaArray=[];
        let header={"Token": this.$store.state.token};
        let configuracion={headers : header}
  
        axios.get('persona/listClientes', configuracion).then(function(response){
            
            
            personaArray=response.data;
            personaArray.map(function (x) {
              me.personas.push({text:x.nombre, value:x._id})
            })
           // console.log(this.ingresos, 'xd')
        }).catch(function(error){
            console.log(error)
        });
    },



    limpiar(){
                this._id='';
                this.tipo_comprobante='';
                this.serie_comprobante='';
                this.num_comprobante='';
                this.impuesto=0.18;
                this.codigo='';
                this.total=0;
                this.totalParcial=0;
                this.totalImpuesto=0;
                this.detalles=[];
                this.verNuevo=0;
                this.valida=0;
                this.validaMensaje=[];
                this.verDetalle=0;
            },
  
            validar(){
                this.valida=0;
                this.validaMensaje=[];
                if(!this.persona){
                    this.validaMensaje.push('Seleccione un cliente.');
                }
                if(!this.tipo_comprobante){
                    this.validaMensaje.push('Seleccione un tipo de comprobante.');
                }
                if(!this.num_comprobante){
                    this.validaMensaje.push('Ingrese el nmero del comprobante.');
                }
                if(!this.impuesto || this.impuesto<0 || this.impuesto>1){
                    this.validaMensaje.push('Ingrese un impuesto vlido.');
                }
                if(this.detalles.length<=0){
                    this.validaMensaje.push('Ingrese al menos un artculo al detalle');
                }
                if (this.validaMensaje.length){
                    this.valida=1;
                }
                return this.valida;
            },

            mostrarNuevo(){
              this.verNuevo=1
            },
            ocultarNuevo(){
              this.verNuevo=0;
              this.limpiar();
            },
  
            guardar(){
                let me=this;
                let header={"Token" : this.$store.state.token};
                let configuracion= {headers : header};
                if (this.validar()){
                    return;
                }
                //Cdigo para guardar
                axios.post('venta/add',
                {
                    'persona':this.persona,
                    'usuario':this.$store.state.usuario._id,
                    'tipo_comprobante': this.tipo_comprobante,
                    'serie_comprobante': this.serie_comprobante,
                    'num_comprobante':this.num_comprobante,
                    'impuesto': this.impuesto,
                    'total':this.total,
                    'detalles':this.detalles
                },configuracion)
                .then(function(response){
                    me.limpiar();
                    me.close();
                    me.listar();
                })
                .catch(function(error){
                    console.log(error);
                });
            },
   
   
  
   
  
    activarDesactivarMostrar(accion, item){
        this.adModal=1
        this.adNombre= item.num_comprobante
        this.adId=item._id
      if (accion==1) {
       this.adAccion=1
      } else if(accion==2){
        this.adAccion=2
      }else{
        this.adModal=0
      }
    },
    cerrarModalActivarDesactivar(){
      this.adModal=0
    },
  
    activar(){
      let me = this;
      let header={"Token": this.$store.state.token};
      let configuracion={headers : header}
      axios.put('venta/activate', {'_id': this.adId}, configuracion)
        .then(function(response){
          me.adModal=0
          me.adAccion=0
          me.adNombre=''
          me.adId=''
          me.listar()
  
        }).catch(function(error){
          console.log(error)
        });
  
  
    },
    desactivar(){
      let me = this;
      let header={"Token": this.$store.state.token};
      let configuracion={headers : header}
      axios.put('venta/desactivate', {'_id': this.adId}, configuracion)
        .then(function(response){
          me.adModal=0
          me.adAccion=0
          me.adNombre=''
          me.adId=''
          me.listar()
  
        }).catch(function(error){
          console.log(error)
        });
  
  
    },
  
   
  
    close () {
      this.dialog = false
   },
   },
  
  }
  
  </script>
